<template>
    <div class="project">
        <div class="loader" v-show="ShowLoader">
            <svg version="1.1" id="loader-1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="40px" height="40px" viewBox="0 0 50 50" style="enable-background:new 0 0 50 50;" xml:space="preserve">
                <path fill="#fff" d="M25.251,6.461c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615V6.461z" transform="rotate(137.96 25 25)">
                    <animateTransform attributeType="xml" attributeName="transform" type="rotate" from="0 25 25" to="360 25 25" dur="0.6s" repeatCount="indefinite"></animateTransform>
                </path>
            </svg>
        </div>
        <navigation></navigation>
        <profile></profile>
        <logo></logo>
        <div class="content_holder container-fluid">
            <form @submit.prevent="Save">
                <div class="col-md-3 col-sm-3 col-xs-12"></div>
                <div class="col-md-9 col-sm-9 col-xs-12">
                    <h4 class="form_title">
                        درج پروژه جدید
                    </h4>
                    <p class="form_desc">
                        با استفاده از این فرم شما می توانید اطلاعات پروژه جدید خود را وارد کنید.
                    </p>
                </div>
                <div class="row">
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">عنوان</label>
                        <input class="form-control col-md-9 col-sm-9 col-xs-12" name="title" v-model="project.title" maxlength="58" placeholder="عنوان" /> 
                    </div>
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">توضیح کوتاه</label>
                        <input class="form-control col-md-9 col-sm-9 col-xs-12" name="title" v-model="project.description" maxlength="150" placeholder="توضیح کوتاه" /> 
                    </div>
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">توضیح تکمیلی</label>
                        <textarea class="form-control col-md-9 col-sm-9 col-xs-12" v-model="project.content" spellcheck="false"></textarea>
                    </div>
                    <!--<div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">تصویر اصلی</label>
                        <file-base64
                            :multiple="false"
                            :done="UploadHeader">
                        </file-base64>
                        <div class="progress gallery_progress">
                            <div class="progress-bar" role="progressbar" aria-valuenow="0"
                            aria-valuemin="0" aria-valuemax="100">
                            </div>
                        </div>
                        <div class="clearfix"></div>
                        <div class="col-md-3 col-sm-3 col-xs-12"></div>
                        <figure>
                            <img :src="project.gallery[0]" class="img-responsive thumbnail"/>
                            <span class="remove_img" @click="RemoveHeader()"></span>
                        </figure>
                    </div>-->
                </div>
                <div class="row">
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">گالری پروژه</label>
                        <file-base64
                            :multiple="false"
                            :done="UploadGallery">
                        </file-base64>
                        <div class="progress gallery_progress">
                            <div class="progress-bar" role="progressbar" aria-valuenow="0"
                            aria-valuemin="0" aria-valuemax="100">
                            </div>
                        </div>
                        <div class="clearfix"></div>
                        <div class="col-md-3 col-sm-3 col-xs-12"></div>
                        <figure v-for="(item, index) in project.gallery">
                            <img :src="item.src" class="img-responsive thumbnail"/>
                            <span class="remove_img" @click="RemoveGallery(index)"></span>
                        </figure>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-3 col-sm-3 col-xs-12"></div>
                    <div class="col-md-9 col-sm-9 col-xs-12" style="padding-right:0px;">
                        <button type="submit" class="btn save">ذخیــره</button>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import Menu from './Menu'
import Logo from './Logo'
import Profile from './Profile'
import fileBase64 from './uplaoder'

export default {
    name:'addproject',
    data (){
        return {
            ShowLoader:false,
            project:{
                title:'',
                description:'',
                content:'',
                gallery:[]
            }
        }
    },
    components:{
        'navigation':Menu,
        'profile':Profile,
        'logo':Logo,
        fileBase64 
    },
    created(){
        return document.title = "درج پروژه جدید";
    },
    methods: {
        UploadGallery(file){
            var formData = new FormData();
            formData.append('file', file.file);
            this.$http.post('http://panel.hex.team/api/upload', formData, {
                headers:{
                    'Authorization':localStorage.getItem('Authorization')
                },
                progress(e) {
                    if (e.lengthComputable) {
                        $('.gallery_progress').css('display','block');
                        $('.gallery_progress .progress-bar').css('width',parseInt((e.loaded / e.total ) * 100)+"%");
                    }
                }
            }).then(function (response){
                $('.gallery_progress').css('display','none');
                $('.gallery_progress .progress-bar').css('width',"0%");
                this.project.gallery.push({src: 'http://panel.hex.team/api/download/' + response.data.name});
            }, function (response) {
                console.log('Error occurred...');
            });
        },
        RemoveGallery(index){
            this.project.gallery.splice(index,1);
        },
        Save(){
            this.ShowLoader = true;
            console.log(this.project);
            this.$http.post('http://panel.hex.team/api/addwidget',{
                widget:'project',
                data:JSON.stringify(this.project)
            },{
                headers: {
                   'Authorization':localStorage.getItem('Authorization') 
                }
            })
            .then(function(res){
                if(res.status == 200){
                    this.ShowLoader = false;
                }
            },function(err){
                console.log(err);
            });
        }
    }
}
</script>

<style>
.project .content_holder{
    position: absolute;
    left: 80px;
    top: 100px;
    right: 40px;
    bottom: 80px;
    padding: 0;
    z-index: 99;
}
.project form{
    float: right;
    width: 100%;
}
.project .form_title{
    text-align: right;
    font-family: "IRANSANS BOLD";
    padding-bottom: 10px;
    font-size: 1.5em;
}
.project .form_desc{
    text-align: right;
    padding-bottom: 10px;
    font-size: 1em;
    direction: rtl;
    margin-bottom: 25px;
}
.project form label{
    float: right;
    margin-bottom: 15px;
    text-align: right;
}
.project form .form-group{
    padding-bottom:15px;
}
.project form .form-control{
    text-align: right;
    outline: none;
    box-shadow: none;
    background-color: #f5f5f5;
    border: 2px solid #cccccc;
    direction: rtl;
    padding: 10px 12px;
    height: 44px;
    border-radius: 6px;
}
.project form textarea.form-control{
    height: 150px;
    padding:20px;
    text-align: left;
    direction: ltr;
    border-radius: 6px;
    resize: vertical;
    padding: 20px;
}
.quill-editor{
    padding: 0;
}
.ql-container{
    background-color: #f5f5f5;
    border-radius: 0 0 6px 6px;
}
.ql-editor.ql-blank::before{
    right: 10px;
}
.ql-toolbar.ql-snow{
    border-radius: 6px 6px 0 0;
    background-color: #f5f5f5;
    direction: rtl;
}
.ql-container .ql-editor {
    min-height: 10em;
    padding-bottom: 1em;
    max-height: 15em;
    font-family: "IRANSANS";
}
.ql-snow .ql-picker:not(.ql-color-picker):not(.ql-icon-picker) svg{
    left: 0;
    right: auto!important;
}
.project figure{
    margin: 0;
    padding: 0;
    position: relative;
    max-height: 150px;
    float: right;
    display: block;
    margin-left: 15px;
}
.project figure:hover .remove_img{
    opacity: 1;
    transition: all .2s ease-in;
}
.project .remove_img{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
    text-align: center;
    background: rgba(255, 255, 255, 0.45);
    transition: all .2s ease-in;
}
.project .remove_img:after{
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    height: 35px;
    width: 60px;
    border-radius: 6px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background-color: #fff;
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;
    background-image: url(data:image/svg+xml;utf8;base64,)
}
.project .thumbnail{
    border-radius: 5px;
    border:0;
    height: auto;
    min-height: 100px;
    min-width: 170px;
    max-width: 170px;
    object-fit: cover;
    object-position: center;
    max-height: 100px;
    padding: 0;
    background: transparent;
    float: right;
    margin-bottom: 0;
}
.project .save{
    background: #f1f102;
    color: #000;
    box-shadow: none;
    outline: none;
    padding: 7px 50px;
    font-size: 15px;
    float: right;
    margin-bottom: 50px;
    margin-right: 10px;
    width: 170px;
    font-family: 'IRANSANS BOLD';
    box-shadow: 0px 5px 0px #d7d710;
    transition: all .2s ease-in;
}
.project .save:hover{
    box-shadow: 0px 3px 0px #d7d710;
    transition: all .2s ease-in;
}
.loader{
    position: fixed;
    z-index: 9999;
}
.progress{
    float: right;
    width: 200px;
    margin-right: 20px;
    height: 20px;
    margin-top: 10px;
    display: none;
    box-shadow: none;
    border: 1px solid #ebebeb;
    position: relative;
}
.progress::before {
    content: 'در حال بارگزاری تصویر';
    background: transparent;
    position: absolute;
    top: 0px;
    left: 0;
    right: 0;
    z-index: 99999;
    text-align: center;
    font-size: 13px;
}
.progress-bar{
    box-shadow: none;
    background-color: #f1f102;
    color: #444;
    line-height: 1.5;
    border-radius: 3px 0 0 3px;
}
.padding-right-none{
    padding-right: 0px;
}
</style>


